.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 38px;
    z-index: 1;
    /* Animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_contents{
    display: flex;
    justify-content: space-between;
    
    
}
.nav_logo{
    position: fixed;
    top: 1px;
    left: 0;
    width: 137px;
    object-fit: contain; padding-left: 20px;
    cursor: pointer;
}
.nav_avatar{
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;height: 30px;
}
.nav_black{
    background-color: #111;
}